@import "../variables";
@import "easepick";

:host {
    --color-fg-primary: #5123A0;
    --color-fg-accent: #F69D0A;
    --color-bg-inrange: #F9F6FE;
    --color-btn-primary-border: #5123A0;
    --color-btn-primary-bg: #5123A0;
    --color-btn-primary-fg: #fff;
    --color-btn-primary-hover-bg: #6C46AE;
    --color-btn-primary-hover-border: #6C46AE;
    --color-btn-secondary-border: #C7BDD9;
    --color-btn-secondary-fg: #777;
    --color-btn-secondary-hover-border: #5123A0;
    --color-btn-secondary-hover-bg: #C7BDD9;
    --color-btn-secondary-hover-fg: #5123A0;
    --color-btn-secondary-hover-bg: #fff;
}
.container.inline {
    box-shadow: none;
}
.day {
    cursor: pointer !important;
}
.preset-plugin-container {
    padding: 12px;
}
.preset-button {
    background: #fff;
    font-size: 14px;
    padding: 4px 8px;
    margin: 0 6px 6px 0;
    border: solid 1px transparent;
    border-radius: 5px;
    color: $color-purple;
    cursor: pointer;

    &:hover {
        color: $color-purple;
        border-color: $color-purple;
    }
}
.footer-buttons {
    justify-content: flex-start !important;
}
.apply-button {
    order: 1;
}
.cancel-button {
    order: 2;
}
.calendar:first-child {
    padding-left: 0;
}
.calendar:last-child {
    padding-right: 0;
}